import { Component, OnInit, OnDestroy  } from '@angular/core';
import * as _ from 'lodash';
import { HttpClient } from '@angular/common/http';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { AuthService } from '../../../core/consultaService/auth.service';
import { AdministracionComercialService } from '../../../core/consultaService/administracionComercial.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ZonasPopUpComponent } from '../../commonPopUp/zonasPopUp/zonasPopUp.component';
import { TerritoriosPopUpComponent } from '../../commonPopUp/territoriosPopUp/territoriosPopUp.component';



import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';

const swal = require('sweetalert');

@Component({
  selector: 'app-estructuraComercial',
  templateUrl: './estructuraComercial.component.html',
  styleUrls: ['./estructuraComercial.component.scss']
})
export class EstructuraComercialComponent implements OnInit, OnDestroy  {

  forma: FormGroup;
  request: any = {};
  requestZonas: any = {};

  tipoFuerzaVenta: Array<any> = [];  

  parametrosUsuario: any;

  zonaList: Array<any> = [];

  territorioList: Array<any> = [];

  subData: any;

  bsModalRef: BsModalRef;

  codFuerzaVenta = "";

  constructor(fb: FormBuilder, private authService:AuthService, private administracionComercialService:AdministracionComercialService,
              public cargador: NgxSpinnerService, private consultasService:ConsultasService, private modalService: BsModalService) {
    this.forma = new FormGroup ({
      fuerzaVenta: new FormControl(),
    });
  }

  ngOnInit() {
    this.parametrosUsuario = this.authService.getDatosUser();
    console.log('parametros Usuario', this.parametrosUsuario);

    this.request.nRutOperador = this.parametrosUsuario.nrutoperador;
    console.log('request rtu operador', this.request);

    this.cargador.show();

    this.administracionComercialService.getFuerzaVentasOperador(this.request)
      .subscribe((data:any)=>{
          console.log(data);
          if(data.estructuraComercial){
            this.cargador.hide();
            this.tipoFuerzaVenta = data.estructuraComercial;
            this.forma.controls.fuerzaVenta.setValue(this.tipoFuerzaVenta[0].ofv_scodfuerzavta);
  
            this.cargarZonasTerritorios();
          }

          if(data.error.codError != 0){
            this.cargador.hide();
            swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
          } 
          
      },
      error =>{
          this.cargador.hide();
          swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
    })
    
    this.subData = this.consultasService.datos$.subscribe(resData =>{
      this.setData(resData);
    })
  }

  ngOnDestroy(){
    if(this.subData != undefined) {
      this.subData.unsubscribe();
    }
  }

  setData(resData){
    if(resData === 'guardar'){
        this.cargarZonasTerritorios();
    }
  }

  cargarZonasTerritorios(){
    this.requestZonas.nRutOperador = this.parametrosUsuario.nrutoperador;
    this.requestZonas.sCodEstado = "";
    this.requestZonas.sCodFuerzaVta = this.forma.controls.fuerzaVenta.value;

    this.codFuerzaVenta = this.forma.controls.fuerzaVenta.value;

    this.cargador.show();

    this.administracionComercialService.getZonasOperador(this.requestZonas)
      .subscribe((data:any)=>{
          this.cargador.hide();
          console.log('zonas:',data);
          this.zonaList = data.zonasOperador;
      },
      error =>{
          this.cargador.hide();
          swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
    })

    this.administracionComercialService.getTerritoriosOperador(this.requestZonas)
    .subscribe((data:any)=>{
          console.log('territorios:',data);
          this.territorioList = data.territoriosVigentes;
    },
    error =>{
        swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
  })

  }

  openEditarZona(zona){
    window.scroll(0,0);
    const initialState = {
      titulo: 'Editar',
      zona: zona,
      codFuerzaVenta: this.codFuerzaVenta
    };
    this.bsModalRef = this.modalService.show(ZonasPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openAgregarZona(){
    const initialState = {
      titulo: 'Agregar',
      codFuerzaVenta: this.codFuerzaVenta
    };
    this.bsModalRef = this.modalService.show(ZonasPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openEditarTerritorio(territorio){
    window.scroll(0,0);
    const initialState = {
      titulo: 'Editar',
      territorio: territorio,
      codFuerzaVenta: this.codFuerzaVenta
    };
    this.bsModalRef = this.modalService.show(TerritoriosPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openAgregarTerritorio(){
    const initialState = {
      titulo: 'Agregar',
      codFuerzaVenta: this.codFuerzaVenta
    };
    this.bsModalRef = this.modalService.show(TerritoriosPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

}
