import { NgModule } from '@angular/core';
import { Routes, RouterModule} from '@angular/router';
import { Ng2TableModule } from 'ng2-table/ng2-table';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { SharedModule } from '../../shared/shared.module';
import { NgxPaginationModule } from 'ngx-pagination';
import { CommonPopUpModule } from '../commonPopUp/commonPopUp.module';


import { DescuentosFuncionalesComponent } from './descuentosFuncionales/descuentosFuncionales.component';
import { AccionesComercialesComponent } from './accionesComerciales/accionesComerciales.component';
import { DetalleDescuentoComercialComponent } from './accionesComerciales/detalleDescuentoComercial/detalleDescuentoComercial.component';
import { EstructuraComercialComponent } from './estructuraComercial/estructuraComercial.component';
import { BuscarArticuloPopUpComponent } from '../commonPopUp/buscarArticuloPopUp/buscarArticuloPopUp.component';
import { CategoriaNegocioPopUpComponent } from '../commonPopUp/categoriaNegocioPopUp/categoriaNegocioPopUp.component';
import { PreciosComponent } from './precios/precios.component';
import { ZonasPopUpComponent } from '../commonPopUp/zonasPopUp/zonasPopUp.component';
import { TerritoriosPopUpComponent } from '../commonPopUp/territoriosPopUp/territoriosPopUp.component';
import { ClienteACPopUpComponent } from '../commonPopUp/clienteACPopUp/clienteACPopUp.component'
import { ClienteRelComercialPopUpComponent } from '../commonPopUp/clienteRelComercialPopUp/clienteRelComercialPopUp.component';
import { DetalleBonificacionComercialComponent } from './accionesComerciales/detalleBonificacionComercial/detalleBonificacionComercial.component';

const routes: Routes = [
  { path: 'accionesComerciales', component: AccionesComercialesComponent },
  { path: 'descuentosFuncionales', component: DescuentosFuncionalesComponent },
  { path: 'estructuraComercialOperador', component: EstructuraComercialComponent },
  { path: 'precios', component: PreciosComponent },
  { path: 'detalleDescuentoComercial', component: DetalleDescuentoComercialComponent },
  { path: 'detalleBonificacionComercial', component: DetalleBonificacionComercialComponent }
];


@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild(routes),
    Ng2TableModule,
    NgxDatatableModule,
    NgxPaginationModule,
    CommonPopUpModule
  ],
  declarations: [
    DescuentosFuncionalesComponent,
    AccionesComercialesComponent,
    DetalleDescuentoComercialComponent,
    EstructuraComercialComponent,
    PreciosComponent,
    DetalleBonificacionComercialComponent
  ],
  
  entryComponents: [ BuscarArticuloPopUpComponent, CategoriaNegocioPopUpComponent, ZonasPopUpComponent,
                     TerritoriosPopUpComponent, ClienteACPopUpComponent, ClienteRelComercialPopUpComponent ]
})
export class AdministracionComercialModule { }
